<template>
  <div>
    <div>
      <el-button @click="freshen" type="primary" size="small" class="margin-right10">刷新当前页</el-button>
      <el-input placeholder="请输入内容" style="width:200px;float:right;" size="small" v-model="input">
        <el-button slot="append" icon="el-icon-search" @click="search()"></el-button>
      </el-input>
    </div>
    <el-table
      empty-text="暂无数据"
      @sort-change="sortChange"
      :data="tableData"
      style="width: 100%; margin-top:10px; margin-bottom: 20px;"
      row-key="id"
      border
      stripe
      :default-sort="{prop: orderName, order: direction}"
    >
      <el-table-column
        v-for="(item, index) in tableLabel"
        :key="index"
        :prop="item.prop"
        :label="item.label"
        sortable="custom"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span v-if="scope.column.property != 'status'">{{scope.row[scope.column.property]}}</span>
          <span v-if="scope.column.property == 'status'">
            <i
              class="el-icon-circle-check"
              style="font-size:22px;color:#FA7070;"
              title="发送成功"
              v-if="scope.row.status=='发送成功'"
            ></i>
            <i
              class="el-icon-circle-close"
              style="font-size:22px;color:#FA7070;"
              title="发送失败"
              v-if="scope.row.status=='发送失败'"
            ></i>
            <i
              class="el-icon-d-arrow-right"
              style="font-size:22px;color:#FA3232;"
              title="发送中"
              v-if="scope.row.status=='发送中'"
            ></i>
            <i
              class="el-icon-document-checked"
              style="font-size:22px;color:#FA7070;"
              title="处理成功"
              v-if="scope.row.status=='处理成功'"
            ></i>
            <i
              class="el-icon-document-delete"
              style="font-size:22px;color:#FFC8C8;"
              title="处理失败"
              v-if="scope.row.status=='处理失败'"
            ></i>
          </span>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align:right;">
      <Page
        v-if="total>pageSize"
        :total="total"
        :current="pageNo"
        @on-change="changePage"
        :page-size="pageSize"
        show-total
        show-elevator
      ></Page>
    </div>
  </div>
</template>
<script>
import { getTable, getTableData } from '@/api/accidents'
export default {
	data() {
		return {
			tableLabel: [],
			tableData: [],
			pageSize: 10,
			pageNo: 1,
			total: 0,
			input: '',
			orderName: 'completetime',
			direction: 'descending',
		}
	},
	mounted() {
		this.init()
	},
	methods: {
		sortChange(scope) {
			this.orderName = scope.prop
			this.direction = scope.order
			this.bindData()
		},
		changePage(e) {
			this.pageNo = e
			this.bindData()
		},
		search() {
			if (!this.input) {
				return this.$Message.info('请输入内容')
			}
			this.pageNo = 1
			this.bindData()
		},
		bindData() {
			let params = [this.pageNo, this.pageSize, this.orderName, this.direction, this.input]
			this.getData(...params)
		},
		getData(pageNo, pageSize, orderName, direction, keyword) {
			getTableData(...arguments).then(res => {
				this.tableData.splice(0, this.pageSize)
				res.recordSet.forEach(column => {
					var item = {}
					this.tableLabel.forEach(label => {
						item[label.prop] = column[label.prop]
					})
					this.tableData.push(item)
				})
				this.total = res.recordsTotal
			})
		},
		init() {
			let params = [this.pageNo, this.pageSize, this.orderName, this.direction, this.input]
			getTable(...params).then(res => {
				res[0].recordSet.forEach(item => {
					if (item.display) {
						this.tableLabel.push({
							prop: item.field,
							label: item.fieldname,
						})
					}
				})
				res[1].recordSet.forEach(column => {
					var item = {}
					this.tableLabel.forEach(label => {
						item[label.prop] = column[label.prop]
					})
					this.tableData.push(item)
					this.total = res[1].recordsTotal
				})
			})
		},
		freshen() {
			this.input = ''
			this.bindData()
		},
	},
}
</script>
